@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend+Mega&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
html,
body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
}

* {
  box-sizing: border-box;
}

.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background: rgb(252, 70, 110);
  background: linear-gradient(
    310deg,
    rgba(252, 70, 110, 1) 0%,
    rgba(159, 228, 111, 1) 90%
  );
}

.joinInnerContainer {
  width: 20%;
}

.joinInput {
  border-radius: 10px;
  padding: 15px 20px;
  width: 100%;
  border-color: transparent;
}

.joinInput:focus {
  border: 1px solid #1670be;
  box-shadow: 0 0 3px #1670be;
  outline-offset: 0px;
  outline: none;
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: 'Raleway', sans-serif;
}

.heading {
  color: white;
  text-shadow: 0 2px 3px rgba(0, 00, 00, 0.5);
  font-size: 3.5em;
  font-family: 'Bebas Neue', cursive;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
  /* letter-spacing: 10px; */
}

.button {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: rgb(252, 70, 110);
  background: linear-gradient(
    310deg,
    rgba(252, 70, 110, 1) 0%,
    rgba(111, 166, 228, 1) 90%
  );
  padding: 20px;
  border-radius: 15px;
  display: inline-block;
  border: none;
  width: 100%;
  transition: ease-in-out;
  transition-duration: 1.4s;
  cursor: pointer;
}

.button:hover {
  background: rgb(70, 181, 252);
  background: linear-gradient(
    310deg,
    rgba(70, 181, 252, 1) 42%,
    rgba(111, 166, 228, 1) 90%
  );
}

.button:active,
.button:focus {
  border: 1px solid #1670be;
  box-shadow: 0 0 3px #1670be;
  border-radius: 15px;
  outline-offset: 0px;
  outline: none;
}

.mt-20 {
  margin-top: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    width: 90%;
  }
}

button:focus {
  outline: 0;
}

.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: rgb(44, 31, 41);
  background: linear-gradient(
    309deg,
    rgba(44, 31, 41, 1) 0%,
    rgba(61, 73, 115, 1) 69%
  );
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 8px;
  height: 60%;
  width: 35%;
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}

.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgb(116, 119, 172);
  background: linear-gradient(
    19deg,
    rgba(116, 119, 172, 1) 67%,
    rgba(133, 176, 118, 1) 100%
  );
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}

.leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 5%;
}

.onlineIcon {
  margin-right: 5%;
}

h3.roomNameText {
  font-family: 'Lexend Mega', sans-serif;
}

.form {
  display: flex;
  border-top: 2px solid #d3d3d3;
}

.input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 80%;
  font-size: 1.2em;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  font-family: 'Hammersmith One', sans-serif;
  font-size: larger;
  background: rgb(117, 130, 201);
  background: linear-gradient(
    10deg,
    rgba(117, 130, 201, 1) 6%,
    rgba(0, 120, 255, 1) 90%
  );
  padding: 20px;
  display: inline-block;
  border: none;
  width: 20%;
}

.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}

.messageBox {
  background: #f3f3f3;
  border-radius: 20px;
  margin: 5px 0;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  font-family: 'Ubuntu', sans-serif;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: rgb(105, 122, 213);
  background: linear-gradient(
    10deg,
    rgba(105, 122, 213, 1) 84%,
    rgba(126, 127, 184, 1) 90%
  );
}

.backgroundLight {
  background: rgb(228, 193, 220);
  background: linear-gradient(
    175deg,
    rgba(228, 193, 220, 1) 73%,
    rgba(228, 193, 220, 1) 100%
  );
}

